import {
  ANIMATION_MODULE_TYPE,
  Directive,
  ElementRef,
  Inject,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Optional,
  Renderer2
} from '@angular/core';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';
import { AriaDescriber, InteractivityChecker } from '@angular/cdk/a11y';
import { DOCUMENT } from '@angular/common';

let nextId = 0;

export type DscBadgePosition =
  | 'above after'
  | 'above before'
  | 'below before'
  | 'below after'
  | 'before'
  | 'after'
  | 'above'
  | 'below';

export type DscBadgeSize = 'small' | 'standard' | 'large';

export type DscBadgeVariant = 'accent' | 'danger' | 'highlight' | 'information' | 'success' | 'warning';

@Directive({
  selector: '[dscBadge]',
  standalone: true,
  inputs: ['disabled: dscBadgeDisabled'],
  host: {
    'class': 'mat-badge',
    '[class.mat-badge-overlap]': 'overlap',
    '[class.mat-badge-variantDark]': 'variantDark',
    '[class.mat-badge-above]': 'isAbove()',
    '[class.mat-badge-below]': '!isAbove()',
    '[class.mat-badge-before]': '!isAfter()',
    '[class.mat-badge-after]': 'isAfter()',
    '[class.mat-badge-small]': 'size === "small"',
    '[class.mat-badge-medium]': 'size === "standard"',
    '[class.mat-badge-large]': 'size === "large"',
    '[class.mat-badge--accent]': 'variant === "accent"',
    '[class.mat-badge--danger]': 'variant === "danger"',
    '[class.mat-badge--highlight]': 'variant === "highlight"',
    '[class.mat-badge--information]': 'variant === "information"',
    '[class.mat-badge--success]': 'variant === "success"',
    '[class.mat-badge--warning]': 'variant === "warning"',
    '[class.mat-badge-hidden]': 'hidden || !content',
    '[class.mat-badge-disabled]': 'disabled',
  },

})
export class DscBadgeDirective implements OnInit, OnDestroy {

  private _isInitialized = false;

  private _badgeElement: HTMLElement | undefined;

  private _inlineBadgeDescription: HTMLElement | undefined;

  private _id: number = nextId++;

  @Input('dscBadgeSize') size: DscBadgeSize = 'standard';

  @Input('dscBadgeVariant') variant: DscBadgeVariant = 'danger';

  @Input('dscBadgeVariantDark')
  get variantDark(): boolean {
    return this._variantDark;
  }
  set variantDark(val: BooleanInput) {
    this._variantDark = coerceBooleanProperty(val);
  }
  private _variantDark: boolean = false;

  @Input('dscBadgeOverlap')
  get overlap(): boolean {
    return this._overlap;
  }
  set overlap(val: BooleanInput) {
    this._overlap = coerceBooleanProperty(val);
  }
  private _overlap: boolean = true;

  @Input('dscBadgePosition') position: DscBadgePosition = 'above after';

  @Input('dscBadge')
  get content(): string | number | undefined | null {
    return this._content;
  }

  set content(newContent: string | number | undefined | null) {
    this._updateRenderedContent(newContent);
  }

  private _content: string | number | undefined | null;

  @Input('dscBadgeDescription')
  get description(): string {
    return this._description;
  }
  set description(newDescription: string) {
    this._updateDescription(newDescription);
  }

  private _description!: string;

  @Input('dscBadgeHidden')
  get hidden(): boolean {
    return this._hidden;
  }

  set hidden(val: BooleanInput) {
    this._hidden = coerceBooleanProperty(val);
  }

  private _hidden!: boolean;

  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(value: any) {
    this._disabled = coerceBooleanProperty(value);
  }

  private _disabled: boolean = false;

  constructor(
    @Inject(DOCUMENT) private _document: Document,
    private _elementRef: ElementRef<HTMLElement>,
    private _renderer: Renderer2,
    private _ariaDescriber: AriaDescriber,
    private _interactivityChecker: InteractivityChecker,
    private _ngZone: NgZone,
    @Optional() @Inject(ANIMATION_MODULE_TYPE) private _animationMode?: string
  ) { }

  ngOnInit(): void {
    this._clearExistingBadges();

    if (this.content && !this._badgeElement) {
      this._badgeElement = this._createBadgeElement();
      this._updateRenderedContent(this.content);
    }

    this._isInitialized = true;
  }

  isAbove(): boolean {
    return this.position.indexOf('below') === -1;
  }

  isAfter(): boolean {
    return this.position.indexOf('before') === -1;
  }

  private _updateRenderedContent(newContent: string | number | undefined | null): void {
    const newContentNormalized: string = `${newContent ?? ''}`.trim();
    if (this._isInitialized && newContentNormalized && !this._badgeElement) this._badgeElement = this._createBadgeElement();
    if (this._badgeElement && this.size !== 'small') this._badgeElement.textContent = newContentNormalized;
    this._content = newContentNormalized;
  }

  private _createBadgeElement(): HTMLElement {
    const badgeElement = this._renderer.createElement('span');
    badgeElement.setAttribute('id', `mat-badge-content-${this._id}`);
    badgeElement.setAttribute('aria-hidden', 'true');
    badgeElement.classList.add('mat-badge-content');
    if (this._animationMode === 'NoopAnimations') badgeElement.classList.add('_mat-animation-noopable');
    this._elementRef.nativeElement.appendChild(badgeElement);
    if (typeof requestAnimationFrame === 'function' && this._animationMode !== 'NoopAnimations') {
      this._ngZone.runOutsideAngular(() => {
        requestAnimationFrame(() => {
          badgeElement.classList.add('mat-badge-active');
        });
      });
    } else {
      badgeElement.classList.add('mat-badge-active');
    }
    return badgeElement;
  }

  private _clearExistingBadges() {
    const badges = this._elementRef.nativeElement.querySelectorAll(':scope > .badge-content',);
    for (const badgeElement of Array.from(badges)) if (badgeElement !== this._badgeElement) badgeElement.remove();
  }

  private _updateDescription(newDescription: string): void {
    this._ariaDescriber.removeDescription(this._elementRef.nativeElement, this.description);
    if (!newDescription || this._isHostInteractive()) this._removeInlineDescription();
    this._description = newDescription;

    if (this._isHostInteractive()) {
      this._ariaDescriber.describe(this._elementRef.nativeElement, newDescription);
    } else {
      this._updateInlineDescription();
    }
  }

  private _isHostInteractive(): boolean {
    return this._interactivityChecker.isFocusable(this._elementRef.nativeElement, { ignoreVisibility: true });
  }

  private _removeInlineDescription() {
    this._inlineBadgeDescription?.remove();
    this._inlineBadgeDescription = undefined;
  }

  private _updateInlineDescription() {
    if (!this._inlineBadgeDescription) {
      this._inlineBadgeDescription = this._document.createElement('span');
      this._inlineBadgeDescription.classList.add('cdk-visually-hidden');
    }

    this._inlineBadgeDescription.textContent = this.description;
    this._badgeElement?.appendChild(this._inlineBadgeDescription);
  }

  ngOnDestroy(): void {
    if (this._renderer.destroyNode) {
      this._renderer.destroyNode(this._badgeElement);
      this._inlineBadgeDescription?.remove();
    }

    this._ariaDescriber.removeDescription(this._elementRef.nativeElement, this.description);
  }
}
